export type InstallAppSuccess = {
  status: 'SUCCESS' | 'UPGRADE_TO_PREMIUM' | 'CONNECT_DOMAIN' | 'CANCELED';
  installedApps?: { appId: string; version?: string }[];
};

export type InstallApp = (options: {
  appId: string;
  version?: string;
  avoidPostInstallation?: boolean;
}) => Promise<InstallAppSuccess>;
